.who-we-are {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 504px;

  &__heading {
    @include font-poppins(32px, 600, 150%, #253757);
  }

  &__text {
    @include font-open-sans(24px, 600, 150%, #6C788b);
    max-width: 754px;
  }

  @media (max-width: 758px) {
    .who-we-are{
      
      &__wrapper {
        margin: 20px 20px 0;
      }

      &__heading {
        text-align: center;
      }
    }
  }
}